.preloader {
   position: fixed;
   top: 0;
   left: 0;
   width: 100%;
   height: 100%;
   z-index: 999;
   background-color: #ffffff;
   display: none;

   &.show {
      display: block;
   }
   &__container {
      width: 100%;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      img {
         width: auto;
         height: auto;
			max-width: 100%;
			max-height: 100%;
      }
   }
}